header{
    z-index: 3;
    position: relative;
    width: 100%;
    height: 156px;
    background: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,0.1);
    .container{ 
        width: 100%;
        max-width: 1050px;
        height: 100%;
        margin: 0 auto;
        .row{
            width: 100%;
            &.row1{
                height: 36px;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                #aside{
                    ul{
                        display: flex;
                        li{
                            font-size: 12px;
                            a{
                                color: rgb(51, 51, 51);                           
                                span{
                                    img{width: 7px;}
                                }
                            }
                            i{
                                margin: 0 10px;
                                color: #ccc;
                            }                        
                        }
                        &:first-child{
                            color: rgb(95,0,128);
                        }
                    }
                }
            }
            &.row2{
                height: 64px;
                display: flex;
                align-items: center;
                > div{
                    height: 48px;
                    &.col1{
                        max-width: 325px;
                        margin: 0 auto 0 0;
                        display: flex;
                        align-items: center;
                        h1{
                            span{
                                margin-right: 17px;
                                img{
                                    width: 82px;
                                }
                            }
                            a{
                                font-size: 17px;
                                font-weight: 600;
                                color: rgb(95,0,128);
                            }
                        }
                        i{
                            margin: 0 10px;
                            color: #ccc;
                        }
                        > a{
                            color: rgb(181,181,181);
                            font-size: 17px;
                            font-weight: 600;
                        }
                    }
                    &.col2{
                        width: 400px;
                        margin: 0 auto;
                        position: relative;
                        input{
                            z-index: 1;
                            position: relative;
                            width: 100%;
                            height: 100%;
                            border: 1px solid rgb(95,0,128);
                            font-size: 16px;
                            border-radius: 5px;
                            padding-left: 15px;
                            &::placeholder{
                                color: #999;
                            }
                        }
                        button{
                                z-index: 2;
                                position: absolute;
                                top: 6px;
                                right: 5px;
                                background: none;
                                border: 0;
                            img{
                                width: 36px;
                            }
                        }
                    }
                    &.col3{
                        width: 325px;
                        margin: 0 0 0 auto;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        a{
                            margin-left: 20px;
                            img{
                                width: auto;
                            }
                        }
                    }
                }
            }
            &.row3{
                height: 56px;
                display: flex;
                justify-content: space-between;
                display: flex;
                align-items: center;
                > div{
                    height: 100%;
                    display: flex;
                    align-items: center;
                    &.left-box{
                        width: 100%;
                        max-width: 225px;

                        justify-content: start;
                        h1{
                            a{
                                img{
                                    width: auto;                                                            
                                }
                                span{
                                    margin-left: 15px;
                                    color: rgb(90, 90, 90);
                                    font-size: 15px;
                                    font-weight: 600;
                                }
                                &:hover{
                                    span{
                                        color: rgb(95,0,128);
                                    }
                                }
                            }
                        }
                    }
                    &.center-box{
                        width: 100%;
                        max-width: 600px;
                        #nav{
                            width: 100%;
                            ul{
                                width: 100%;
                                display: flex;
                                li{
                                    width: 25%;
                                    text-align: center;
                                    a{
                                        font-size: 16px;
                                        color: rgb(51, 51, 51);
                                        font-weight: 600;
                                        padding-bottom: 1px;
                                        border-bottom: 1px solid transparent;
                                        &:hover{
                                                color: rgb(95,0,128);
                                                border-color:rgb(95,0,128);
                                        }
                                    }
                                    
                                }
                            }
                        }
                    }
                    &.right-box{
                        width: 100%;
                        max-width: 225px;
                        justify-content: flex-end;
                        span{
                            font-size: 13px;
                            padding: 6px 10px;
                            border: 1px solid#ccc;
                            border-radius: 10.5px;
                            strong{
                                color: rgb(95,0,128);
                            }
                            em{
                                color: rgb(102, 102, 102);
                                margin-left: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
}